












































































































import iPrice from '@/components/i-price/i-price.vue'
export default {
	components: {
		iPrice
	},
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: false, //是否显示面板指示点
			autoplay: true,  //是否自动切换
			circular:true,  //是否衔接切换
			interval: 3000,  //自动切换时间间隔
			duration: 500  ,//滑动动画时长
			ListOfGoods:'', //商品列表
			wheelPlanting: '', //轮播图片
		}
	},
	methods: {
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		changePersonal(e) { //option为object类型，会序列化上个页面传递的参数
			uni.switchTab({
				url: '../user/user'
			});
		},
		changeShopping(e) { //option为object类型，会序列化上个页面传递的参数
			uni.switchTab({
				url: '../shopping/shopping'
			});
		},	
		classification(e){
			uni.switchTab({
				url: '../product/product'
			})
		},
		leftBars(e){
			uni.switchTab({
				url: "../product/product"
			})
		}
	},
	// 数据接口请求( 初始化页面完成时触发)
	onShow(){
		uni.request({
			url: 'http://192.168.199.194/mobile/index/index',
			method: 'GET',
			data: {},
			success: res => {
				if(res.statusCode == 200){
					// console.log(res.data)
					// 商品列表
					this.ListOfGoods = res.data.goods;
					// console.log(res.data.goods)
					// 轮播图片
					this.wheelPlanting = res.data.lun;
				}
			},
			fail: () => {},
			complete: () => {}
		});		
	}
}
